<template>
    <div>
        <!---->
        <div class="tools" v-if="!hideTools">
            <el-row style="padding-top: 10px">
                <el-col :span="24">
                    <!---->
                    <el-input class="hidden-md-and-down" v-model="quickSearch" clearable prefix-icon="el-icon-search"
                              size="small" style="margin-right: 10px;width: 200px;" placeholder="" @change="handleFilter" v-if="quickSearchOn"></el-input>
                    <el-button class="hidden-md-and-down" type="primary" size="small" icon="el-icon-search" @click="handleFilter" v-if="quickSearchOn"></el-button>
                    <!---->
                    <render v-if="addButton" :data="addButton" :slot-props="grid"></render>
                    <!---->
                    <el-dropdown trigger="click" v-if="export">
                        <el-button type="primary" size="small" icon="el-icon-download">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </el-button>
                        <template #dropdown>
                            <el-dropdown-menu>
                                <el-dropdown-item @click.native="exportData('page')"></el-dropdown-item>
                                <el-dropdown-item @click.native="exportData('select')" v-show="selectIds.length > 0"></el-dropdown-item>
                                <el-dropdown-item @click.native="exportData('all')"></el-dropdown-item>
                            </el-dropdown-menu>
                        </template>
                    </el-dropdown>
                    <el-button plain size="small" icon="el-icon-delete" v-if="!hideDeleteSelection && selectIds.length > 0" @click="deleteSelect"></el-button>
                    <el-button plain size="small" icon="el-icon-help" v-if="trashed && selectIds.length > 0" @click="recoverySelect"></el-button>
                    <el-button type="danger" size="small" icon="el-icon-delete" v-if="!hideDeleteButton" @click="deleteAll()">{{trashed && !hideTrashed?'':''}}</el-button>
                    <div style="float: right;margin-right: 15px">
                        <el-tooltip placement="top" :content="trashed?'':''"  v-if="!hideTrashed">
                            <el-button :type="trashed?'primary':'info'" size="mini" circle :icon="trashed?'el-icon-s-grid':'el-icon-delete'" @click="trashedHandel"></el-button>
                        </el-tooltip>
                        <!---->
                        <el-button icon="el-icon-refresh" size="mini" circle style="margin-right: 10px"
                                   @click="loading=true"></el-button>
                        <!---->
                        <el-dropdown trigger="click" :hide-on-click="false">
                            <el-button icon="el-icon-s-grid" size="mini"></el-button>
                            <template #dropdown>
                                <el-dropdown-menu>
                                    <el-checkbox-group v-model="checkboxColumn">
                                        <el-dropdown-item v-for="item in columns">
                                            <el-checkbox :label="item.prop" v-if="item.label">{{item.label}}</el-checkbox>
                                        </el-dropdown-item>
                                    </el-checkbox-group>
                                </el-dropdown-menu>
                            </template>
                        </el-dropdown>
                    </div>
                    <el-button v-if="filter" type="primary" size="small" icon="el-icon-zoom-in" @click="visibleFilter"></el-button>

                    <render v-for="tool in tools" :data="tool" :ids="selectIds" :grid-params="params"></render>
                </el-col>
            </el-row>
        </div>
        <!---->
        <div class="filter" v-if="filter && filterShow">
            <render :data="filter" ></render>
        </div>
        <!---->
        <a-table :row-selection="rowSelection" @change="tableChange" :columns="tableColumns" :data-source="tableData" :pagination="false" v-loading="loading" v-bind="$attrs" row-key="id" ref="dragTable">
            <template v-for="column in tableColumns" v-slot:[column.dataIndex]>
                <render :data="column.header" :slot-props="grid"></render>
            </template>
            <template  #default="{ text , record , index }">
                 <render :data="text" :slot-props="grid"></render>
            </template>
            <template #sortDrag="{ text , record , index }">
                <div style="display: flex;flex-direction: column">
                    <el-tooltip  effect="dark" content="" placement="right-start"><i @click="sortTop(index,record)" class="el-icon-caret-top" style="cursor: pointer"></i></el-tooltip>
                    <el-tooltip effect="dark" content="" placement="right-start"><i class="el-icon-rank sortHandel" style="font-weight:bold;cursor: grab"></i></el-tooltip>
                    <el-tooltip  effect="dark" content="" placement="right-start"><i @click="sortBottom(index,record)" class="el-icon-caret-bottom" style="cursor: pointer"></i></el-tooltip>
                </div>
            </template>
            <template #sortInput="{ text , record , index }">
                   <el-input v-model="text.content.default[0]" @change="sortInput(record.id,text.content.default[0])"></el-input>
            </template>
        </a-table>

        <!---->
        <el-pagination class="pagination"
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       v-if="pagination"
                       v-bind="pagination"
                       :total="total"
                       :page-size="size"
                       :current-page="page">
        </el-pagination>
    </div>
</template>

<script>
    import {defineComponent, ref, watch, inject,nextTick,triggerRef,computed,reactive,unref} from "vue"
    import render from "@/components/render.vue"
    import {useHttp} from '@/hooks'
    import request from '@/utils/axios'
    import {store} from '@/store'
    import {unique,deleteArr} from '@/utils'
    import {ElMessageBox,ElMessage} from 'element-plus'
    import Sortable from 'sortablejs'
    import {useRoute} from 'vue-router'
    export default defineComponent({
        name: "EadminGrid",
        components: {
            render,
        },
        props: {
            data: Array,
            columns: Array,
            pagination: [Object, Boolean],
            modelValue: Boolean,
            loadDataUrl: String,
            hideTools: Boolean,
            export: Boolean,
            sortDrag: Boolean,
            sortInput: Boolean,
            tools:[Object,Array],
            hideSelection: Boolean,
            selectionType:{
                type:String,
                default:'checkbox'
            },
            selection:{
                type:Array,
                default:[]
            },
            hideDeleteButton: Boolean,
            hideTrashed: Boolean,
            hideDeleteSelection: Boolean,
            filter: [Object, Boolean],
            addButton: [Object, Boolean],
            filterField:String,
            params:Object,
        },
        inheritAttrs: false,
        emits: ['update:modelValue','update:selection'],
        setup(props, ctx) {
            const route = useRoute()
            const state = inject(store)
            const proxyData = state.proxyData
            const dragTable = ref('')
            const grid = {grid:ctx.attrs.eadmin_grid}
            const {loading,http} = useHttp()
            const filterShow = ref(false)
            const quickSearch = ref('')
            const selectIds = ref(props.selection || [])
            const eadminActionWidth = ref(0)
            const trashed = ref(false)
            const quickSearchOn = ctx.attrs.quickSearch
            const columns = ref(props.columns)
            const tableData = ref(props.data)
            const total = ref(props.pagination.total || 0)
            let page = 1
            let size = props.pagination.pageSize
            let sortableParams = {}
            watch(() => props.modelValue, (value) => {
                if(value){
                    quickSearch.value = ''
                    loading.value = value
                }
            })

            watch(loading, (value) => {
                if (value) {
                    loadData()
                }
            })
            //
            const checkboxColumn = ref([])
            checkboxColumn.value = props.columns.map(item => {
                return item.prop
            })
            const tableColumns = computed(()=>{
                return columns.value.filter(item=>{
                    if(item.prop === 'EadminAction'){
                        item.width = eadminActionWidth.value
                    }
                    return checkboxColumn.value.indexOf(item.prop) >= 0
                })

            })
            nextTick(()=>{
                //
                document.getElementsByClassName('EadminAction').forEach(item=>{
                    if(eadminActionWidth.value < item.offsetWidth){
                        eadminActionWidth.value = item.offsetWidth
                    }
                })
                eadminActionWidth.value += 30
                //dragSort()
            })
            //
            function dragSort(){
                const el = dragTable.value.$el.querySelectorAll('.ant-table-body > table > tbody')[0]
                Sortable.create(el, {
                    handle:'.sortHandel',
                    ghostClass: 'sortable-ghost', // Class name for the drop placeholder,
                    onEnd: evt => {
                        var newIndex = evt.newIndex;
                        var oldIndex = evt.oldIndex;
                        var oldItem = tableData.value[oldIndex]
                        var startPage = (page-1) * size
                        const targetRow = tableData.value.splice(evt.oldIndex, 1)[0]
                        tableData.value.splice(evt.newIndex, 0, targetRow)
                        if(evt.newIndex != evt.oldIndex){
                            sortRequest(oldItem.id,startPage +newIndex).catch(()=>{
                                const targetRow = tableData.value.splice(evt.newIndex, 1)[0]
                                tableData.value.splice(evt.oldIndex, 0, targetRow)
                            })
                        }
                    }
                })
            }
            function sortRequest(id,sort) {
                return new Promise((resolve, reject) =>{
                    request({
                        url: 'eadmin/batch.rest',
                        params:Object.assign(props.params,route.query),
                        method: 'put',
                        data:{
                            action:'eadmin_sort',
                            id:id,
                            sort: sort,
                            eadmin_ids:[]
                        }
                    }).then(res=>{
                        resolve(res)
                    }).catch(res=>{
                        reject(res)
                    })
                })
            }
            //
            function sortTop(index,data){
                sortRequest(data.id,0).then(res=>{
                    if(page === 1){
                        const targetRow = tableData.value.splice(index, 1)[0]
                        tableData.value.unshift(targetRow)
                    }else{
                        tableData.value.splice(index,1)

                    }
                })

            }
            //
            function sortBottom(index,data){
                sortRequest(data.id,total.value-1).then(res=>{
                    if(page === 1){
                        const targetRow = tableData.value.splice(index, 1)[0]
                        tableData.value.push(targetRow)
                    }else{
                        tableData.value.splice(index,1)
                    }

                })
            }
            //
            function sortInput(id,sort){
                sortRequest(id,sort)
            }
            //
            function handleSizeChange(val) {
                page = 1
                size = val
                loading.value = true
            }
            //
            function handleCurrentChange(val) {
                page = val
                loading.value = true
            }

            const rowSelection = computed(()=>{
                if(props.hideSelection){
                    return null
                }else{
                    return {
                        selectedRowKeys:unref(selectIds),
                        type:props.selectionType,
                        // Checkbox 
                        onSelect: (record, selected, selectedRows, nativeEvent) => {
                            const ids = selectedRows.map(item=>{
                                return item.id
                            })
                            if(selected){
                                if(props.selectionType === 'checkbox'){
                                    selectIds.value = unique(selectIds.value.concat(ids))
                                }else{
                                    selectIds.value = ids
                                }
                            }else{
                                deleteArr(selectIds.value,record.id)
                            }
                            ctx.emit('update:selection',selectIds.value)
                        },
                        onSelectAll:(selected, selectedRows, changeRows)=>{
                            const ids = selectedRows.map(item=>{
                                return item.id
                            })
                            if(selected){
                                selectIds.value = unique(selectIds.value.concat(ids))
                            }else{
                                changeRows.map(item=>{
                                    deleteArr(selectIds.value,item.id)
                                })
                            }
                            ctx.emit('update:selection',selectIds.value)
                        },
                    }
                }
            })
            //
            function handleFilter() {
                page = 1
                loading.value = true
                //
                proxyData[props.filterField] = {}
            }
            //
            function loadData() {
                let requestParams = {
                    ajax_request_data: 'page',
                    page: page,
                    size: size,
                }
                requestParams = Object.assign(requestParams, proxyData[props.filterField],{quickSearch:quickSearch.value},props.params,route.query,sortableParams)
                if(trashed.value){
                    requestParams = Object.assign(requestParams ,{eadmin_deleted:true})
                }
                http({
                    url: props.loadDataUrl,
                    params: requestParams
                }).then(res => {
                    columns.value = res.columns
                    tableData.value = res.data
                    triggerRef(tableData)
                    total.value = res.total
                }).finally(() => {
                    ctx.emit('update:modelValue', false)
                })
            }
            //
            function trashedHandel() {
                trashed.value = !trashed.value
                loading.value = true
            }
            //
            function deleteAll(){
                deleteRequest(', ?',true)

            }
            //
            function deleteSelect() {
                deleteRequest(', ?',selectIds.value)
            }
            //
            function recoverySelect() {
                ElMessageBox.confirm('','?',{type: 'warning'}).then(()=>{
                    request({
                        url: props.loadDataUrl.replace('.rest','/batch.rest'),
                        data: Object.assign({eadmin_ids: selectIds.value},props.params,{delete_time:null}),
                        method:'put',
                    }).then(res=>{
                        loadData()
                    })
                })
            }
            //
            function deleteRequest(message,ids) {

                ElMessageBox.confirm(message,'?',{type: 'warning'}).then(()=>{
                    let params = {}
                    if(trashed.value){
                        params.trueDelete = true
                    }
                    request({
                        url: props.loadDataUrl.replace('.rest','/delete.rest'),
                        data: Object.assign({eadmin_ids: ids},props.params,params),
                        method:'delete',
                    }).then(res=>{
                        selectIds.value = []
                        loadData()
                    })
                })
            }
            //
            function tableChange(pagination, filters, sorter) {
                if(sorter.order === 'descend'){
                    sortableParams = {
                        eadmin_sort_field:sorter.field,
                        eadmin_sort_by:'desc'
                    }

                }else if(sorter.order === 'ascend'){
                    sortableParams = {
                        eadmin_sort_field:sorter.field,
                        eadmin_sort_by:'asc'
                    }
                }else{
                    sortableParams = {}
                }
                loading.value = true
            }
            //
            function exportData(type){
                if(tableData.value.length == 0){
                    ElMessage.warning('')
                    return false
                }
                let  requestParams = {
                        eadmin_export:true,
                        export_type:type,
                        page:page,
                        size:size,
                        Authorization:ctx.attrs.Authorization,
                        eadmin_ids:selectIds.value
                }
                requestParams = Object.assign(requestParams,props.params,route.query)
                let querys = []
                for(var params in requestParams){
                    querys.push(params+'='+requestParams[params])
                }
                location.href = 'http://e-admin.test/eadmin.test?' +querys.join('&')
            }
            function visibleFilter() {
                filterShow.value = !filterShow.value
            }
            return {
                grid,
                eadminActionWidth,
                quickSearchOn,
                page,
                size,
                total,
                handleFilter,
                tableColumns,
                checkboxColumn,
                handleSizeChange,
                handleCurrentChange,
                loading,
                tableData,
                quickSearch,
                rowSelection,
                visibleFilter,
                filterShow,
                deleteSelect,
                recoverySelect,
                deleteAll,
                selectIds,
                dragTable,
                sortTop,
                sortBottom,
                sortInput,
                tableChange,
                trashedHandel,
                trashed,
                exportData
            }
        }
    })
</script>

<style scoped>
    .sortable-selecte{
        background-color: #EBEEF5 !important;
    }
    .sortable-ghost{
        opacity: .8;
        color: #fff!important;
        background: #2d8cf0!important;
    }
    .pagination {
        background: #fff;
        padding: 10px 16px;
        border-radius: 5px;
    }
    .tools {
        background: #fff;
        position: relative;
        border-radius: 4px;
        padding-left: 10px;
        padding-bottom: 10px;
    }
    .filter{
        border-top: 1px solid #ededed;
        background: #fff;
    }

</style>
