<template>
    <div class="tagsView">
        <!--<el-scrollbar class="scroll-container">-->
        <ul>
            <li v-for="item in state.mainComponent" @mouseover="selectTag(item.url)" @mouseout="selectTag('')" @click="clickHandel(item.url)" :class="[route.fullPath ===item.url ? 'activte':'']">
                <span>{{item.title}}</span>
                <i class="el-icon-close close" v-show="route.fullPath === item.url || select === item.url" @click.stop="close(item.url)"></i>
            </li>

        </ul>
        <!--</el-scrollbar>-->
        <div class="breadcrumb">
<!--            <breadcrumb style="margin-right: 10px"></breadcrumb>-->
            <i class="el-icon-back back" @click="back"></i>
            <el-dropdown @command="handleCommand">
                <i class="el-icon-close back"></i>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item icon="el-icon-close" command="other"></el-dropdown-item>
                        <el-dropdown-item icon="el-icon-back" command="left"></el-dropdown-item>
                        <el-dropdown-item icon="el-icon-right" command="right"></el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>

        </div>
    </div>
</template>

<script>
    import {useRoute,useRouter} from 'vue-router'
    import {defineComponent, inject,ref} from 'vue'
    import {store,action} from '@/store'
    import breadcrumb from '@/components/breadcrumb.vue'
    export default defineComponent({
        name: "TagsView.vue",
        components: {
            breadcrumb,
        },
        setup() {
            const route = useRoute()
            const router = useRouter()
            const state = inject(store)
            const select = ref('')
            function clickHandel(url) {
                router.push(url)
            }
            function selectTag(url) {
                select.value = url
            }
            function close(url) {
                if(state.mainComponent.length > 1){
                    action.clearComponent(url)
                    if(route.fullPath === url){
                        const component = state.mainComponent[state.mainComponent.length-1]
                        router.push(component.url)
                    }
                }
            }
            function back() {
                router.back()
            }
            function handleCommand(command) {
                if(command === 'left'){
                    const index = action.getComponentIndex(route.fullPath)
                    state.mainComponent.splice(0,index)
                    state.componentVariable.splice(0,index)
                }else if(command === 'right'){
                    const index = action.getComponentIndex(route.fullPath)
                    state.mainComponent.splice(index+1)
                    state.componentVariable.splice(index+1)
                }else if(command === 'other'){
                    state.mainComponent = []
                    state.componentVariable = []
                    router.push(route.fullPath)
                }
            }
            return {
                handleCommand,
                back,
                close,
                selectTag,
                route,
                state,
                clickHandel,
                select
            }
        }
    })
</script>
<style lang="scss" scoped>
    @import '../styles/element-variables.scss';
    .tagsView {
        z-index: 1;
        display: flex;
        align-items: center;
        height: 45px;
        background: #FFFFFF;
        border-top: 1px solid #f6f6f6;
        box-shadow: rgba(0, 21, 41, 0.08) 0px 1px 4px;
    }
    .scroll-container {
        white-space: nowrap;
        flex: 1;

    }
    .tagsView ul {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        align-items: flex-end;
        height: 45px;
        flex: 1;
    }

    .tagsView li {
        white-space: nowrap;
        display: flex;
        align-items: center;
        font-size: 14px;
        height: 36px;
        margin-right: -18px;
        padding: 0 30px;
        -webkit-mask-size: 100% 100%;
        mask-size: 100% 100%;
        -webkit-mask-image:url('data:image/png;base64,');
        mask-image:url('data:image/png;base64,');
    }
    .tagsView li:hover{
        cursor: pointer;
        color: #515a6e;
        background: #dee1e6;
    }
    .close{
        margin-left: 10px;
        margin-top: 3px;
        font-size: 12px;
    }
    .close:hover{
        padding: 1px;
        border-radius: 50%;
        color: #FFFFFF;
        background: #c0c4cc;
    }
    .activte{
        color: $--color-primary !important;
        background: #e8f4ff !important;
    }
    .breadcrumb{
        display: flex;
        align-items: center;
        margin-right: auto;
        justify-content: flex-end;
    }
    .back{
        display: flex;
        justify-content: center;
        align-items: center;
        border-left: 1px solid #ededed;
        height: 43px;
        width: 40px;
        cursor: pointer
    }
    .back:hover{
        background: #EFEFEF;
    }
</style>
